body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-textarea--outlined .f-textarea__field {
  color: inherit;
  background-color: white;
  border: 1px solid silver;
  border-radius: 0.25em;
  transition: border-color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: border-color; }
  .f-textarea--outlined .f-textarea__field:hover {
    border-color: #6fcf97; }
  .f-textarea--outlined .f-textarea__field:focus {
    border-color: #6fcf97;
    box-shadow: inset 0 0 0 calc(2px - 1px) #6fcf97; }
  .f-textarea--outlined .f-textarea__field[disabled] {
    cursor: not-allowed;
    border-color: gainsboro; }
  .f-textarea--outlined .f-textarea__field:-webkit-autofill, .f-textarea--outlined .f-textarea__field:-webkit-autofill:hover {
    -webkit-box-shadow: 0 0 0 10em white inset !important; }
  .f-textarea--outlined .f-textarea__field:-webkit-autofill:focus, .f-textarea--outlined .f-textarea__field:-webkit-autofill:active {
    -webkit-box-shadow: inset 0 0 0 calc( 2px - 1px) #6fcf97, inset 0 0 0 10em white !important; }

.f-textarea--outlined.has-error .f-textarea__field,
.f-textarea--outlined.has-error .f-textarea__field:hover,
.f-textarea--outlined.has-error .f-textarea__field:focus {
  border-color: #e91e63;
  box-shadow: inset 0 0 0 calc(2px - 1px) #e91e63; }

.f-textarea--outlined .f-textarea__label {
  position: absolute;
  display: flex;
  align-items: center;
  pointer-events: none;
  transition: transform 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: transform;
  transform-origin: top left;
  top: 1em;
  left: 0.9em;
  line-height: 1.5; }

.f-textarea--outlined .f-textarea__field:focus + .f-textarea__label,
.f-textarea--outlined .f-textarea__field.is-filled + .f-textarea__label {
  transform: translateY(-1em); }

.f-textarea--outlined .f-textarea__label-text {
  display: block;
  transform-origin: top left;
  transition: transform 0.2s cubic-bezier(0.51, 0.92, 0.24, 1), color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: transform, color; }
  .f-textarea--outlined .f-textarea__label-text:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -0.35em;
    right: -0.35em;
    z-index: -1;
    background-color: white;
    will-change: transform; }

.f-textarea--outlined .f-textarea__field:hover + .f-textarea__label .f-textarea__label-text {
  color: #6fcf97; }

.f-textarea--outlined .f-textarea__field.is-filled + .f-textarea__label .f-textarea__label-text {
  color: inherit; }

.f-textarea--outlined .f-textarea__field:focus + .f-textarea__label .f-textarea__label-text,
.f-textarea--outlined .f-textarea__field.is-filled + .f-textarea__label .f-textarea__label-text {
  transform: scale(0.8) translateY(-50%) translateY(-1px);
  color: #6fcf97; }

.f-textarea--outlined.has-error .f-textarea__field + .f-textarea__label .f-textarea__label-text {
  color: #e91e63; }

.f-textarea--outlined .f-textarea__indicator {
  position: absolute;
  transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: color; }
  .f-textarea--outlined .f-textarea__indicator--optional {
    top: 1em;
    right: 1em; }
  .f-textarea--outlined .f-textarea__indicator--required {
    top: 0.4em;
    right: 0.4em; }

.f-textarea--outlined .f-textarea__field:hover ~ .f-textarea__indicator {
  color: #6fcf97; }

.f-textarea--outlined .f-textarea__field:focus ~ .f-textarea__indicator {
  color: #6fcf97; }

.f-textarea--outlined .f-textarea__message {
  margin: 0.5em 0 0 0.5em; }
